import React from "react";
import styled from "styled-components";

import Wrapper from "../wrapper/Wrapper";

const SkillsStyled = styled.div`
  .grid-wrapper-small {
    grid-template-rows: auto repeat(6, 260px);
    grid-row-gap: 12px;
  }

  h2 {
    margin: 10px 0 10px 20px;
    font-family: var(--font-family-Roboto-Slab);
    font-size: 36px;
    font-weight: 400;
    color: var(--simple-white);
  }

  .skill-blocks {
    padding: 0 12px;
  }

  .skill-card-grid {
    display: grid;
    grid-template-rows: 0.9fr 1.1fr;
    height: 100%;
    background-color: var(--font-brown-1);
    border-radius: 0 15px;
  }

  .skill-grid-img-1 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 0 25px;
    background-color: var(--simple-white);
    border-radius: 0 15px;
  }

  .skill-grid-img-1 > div {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .skill-grid-img-1 > div > svg {
    height: 80px;
  }

  .skill-grid-text-1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px 15px
  }

  .skill-grid-text-1 > p {
    margin: 0;
    font-family: var(--font-family-Roboto-Slab);
    font-size: 16px;
    font-weight: 500;
  }

  .skill-blocks-small {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 12px;
    padding: 0 12px;
  }

  .skill-grid-img-small {
    display: grid;
    grid-template-columns: 1fr;
  }

  .skill-grid-text-small > p {
    font-size: 14px;
  }

  .grid-wrapper-big {
    display: none;
  }

  @media (min-width: 541px) {
    .grid-wrapper-small {
      grid-template-rows: auto repeat(3, 300px) repeat(3, 260px);
      width: 65%;
      grid-row-gap: 25px;
    }

    .skill-card-grid {
      grid-template-rows: 1.1fr 0.9fr;
    }

    .skill-blocks-small {
      grid-column-gap: 25px;
    }

    .skill-grid-img-1 > div > svg {
      height: 90px;
    }
  }

  @media (min-width: 1200px) {
    .grid-wrapper-small {
      display: none;
    }
    
    .grid-wrapper-big {
      width: 85%;
      margin-top: 5%;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: 320px 320px;
      grid-column-gap: 1.80vw;
      grid-row-gap: 1.80vw;
    }

    .skill-blocks-small-container {
      grid-column: 1 / 4;
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      grid-column-gap: 1.80vw;
    }
    
    .skill-blocks {
      padding: 0;
    }
    
    
    .skill-blocks-small {
      display: block;
      padding: 0;
    }

    .skill-grid-img-1 > div > svg {
      height: 5.2vw;
    }

    .skill-grid-text-1 > p {
      padding: 8px;
      font-size: 18px;
    }

    .skill-grid-text-small > p {
      padding: 5px;
      font-size: 16px;
    }

    .skill-card-grid {
      grid-template-rows: 160px 160px;
      height: 100%;
      
    }
  }

  @media (min-width: 3000px) {
    
    .skill-grid-img-1 > div > svg {
      height: 3vw;
    }
  }

`

